import React, { FC } from 'react';
import {
  CellEditProps,
  ClickEnum,
  ColumnsProps,
  decimalFormatter,
  DefaultSortProps,
  FilterType,
  getExtraData,
  OrderType,
  referenceColumn,
  SearchDataGrid,
  SearchViewProps,
  WindowComponent,
} from 'summer';
import { CLASS_NAME, CreditoCiap } from '../../../../models/dtos/mlf/creditoCiap';

const defaultSorted: DefaultSortProps[] = [
  {
    dataField: 'referencia',
    order: OrderType.DESC,
  },
];

const cellEditProps: CellEditProps = {
  blurToSave: true,
  mode: ClickEnum.CLICK,
};

const CreditoCiapSearchView: FC<SearchViewProps<CreditoCiap>> = props => {
  const columns: ColumnsProps[] = [
    referenceColumn({
      dataField: 'referencia',
      editable: false,
      sort: true,
      text: 'ReferÃªncia',
    }),
    {
      dataField: 'vrCredito',
      editable: false,
      filter: {
        type: FilterType.NUMBER,
      },
      formatter: decimalFormatter,
      sort: true,
      text: 'Valor',
    },
  ];
  return (
    <WindowComponent title="Saldo Anterior do CIAP">
      <SearchDataGrid
        {...props}
        {...props.searchDataGridProps}
        dtoClassName={CLASS_NAME}
        columns={columns}
        cellEditProps={cellEditProps}
        defaultSorted={defaultSorted}
        extraData={getExtraData(props, 'CreditoCiapEditView', CLASS_NAME)}
      />
    </WindowComponent>
  );
};

export default CreditoCiapSearchView;
